import React from 'react';
//import {Link} from 'react-router-dom'
import '../styles/Navbar.css'

const Navbar =()=>{
    return(
       <nav className="navbar navbar-expand-lg navbar-dark bg-dark navbar-static navbar-static-top navbar-fixed-top">
    <a className="navbar-brand" href="/Home"><img src={require('../images/CN.png')} alt="home_img" className='img-fluid home-img'/></a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarColor01">
    <ul className="navbar-nav mr-auto">
      <li className="nav-item">
        <a className="nav-link" href="#AboutMe">About Me <span className="sr-only"></span></a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="#Hobbies">Hobbies</a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="#Portfolio">Portfolio</a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="#Curriculum">Curriculum</a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="#ContactMe">Let's talk</a>
      </li>
    </ul>
  </div>
</nav> 
);
}

export default Navbar;